.menu-container {
  position: relative;
  display: inline-block;
}

.menu-button {
  font-family: cursive;
  margin-right: 10px;
  align-items: center;
  justify-content: center;
  height: 30px;
  font-size: 16px;
  background: linear-gradient(90deg, #ff9a8b, #ff6a88, #ff99ac); /* Sunset gradient */
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.menu-button:hover {
  transform: scale(1.1);
  background: linear-gradient(90deg, #ff6a88, #ff9a8b); /* Reverse gradient */
  box-shadow: 0 6px 10px rgba(255, 154, 139, 0.4);
}

/* Dropdown Menu */
.dropdown-menu {
  position: absolute;
  top: 50px; /* Adjust based on menu-button height */
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align buttons to the left */
  gap: 10px; /* Add spacing between buttons */
  padding: 10px;
  z-index: 1000;
}

/* Buttons inside the dropdown */
.dropdown-menu button {
  font-family: cursive;
  padding: 10px 20px;
  background: linear-gradient(90deg, #ff9a8b, #ff6a88, #ff99ac);
  border: none; /* No border */
  color: white; /* White text */
  font-size: 16px;
  text-align: left;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
  border-radius: 5px; /* Rounded corners for buttons */
}

/* Hover effect for buttons */
.dropdown-menu button:hover {
  background-color: rgba(255, 255, 255, 0.2); /* Slight white overlay */
  transform: scale(1.05); /* Slight enlargement */
}

/* Focus state for buttons */
.dropdown-menu button:focus {
  outline: none; /* Remove default outline */
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.4); /* Glow effect */
}

media-theme-sutro {
  width: 100%; /* Full width of the container */
